.wrap {
  overflow: hidden;
}
.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 135px 0 120px;
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
  &__left {
    width: 50%;
    margin-right: 15px;
    margin-left: auto;
  }
  &__right {
    width: 50%;
    .main__info {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 445px;
      min-height: 125px;
      display: flex;
      align-items: center;
      background: #FFF9E8;
      border: 0px solid #000;
      border-radius: 70px 0px 0px 0px;
      padding: 5px 20px;
      margin: 0;
    }
    .main__info-icon {
      width: 32px;
    }
    .main__info-text {
      font-size: 20px
    }
  }
  &__desc {
    max-width: 740px;
    margin: 0 0 0 auto;
  }
  &__title {
    font-family: 'Crimson';
    font-size: 40px;
    line-height: 120%;
    font-weight: 400;
    color: #3C2C26;
    span {
      font-size: 64px;
      line-height: 120%;
      text-transform: uppercase;
    }
  }
  &__text {
    font-size: 30px;
    line-height: 120%;
    color: #333;
    margin-top: 60px;
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 90px;
  }
  &__btn {
    width: 211px;
    height: 49px;
    background: #776053;
    border: 1px solid transparent;
    border-radius: 50px;
    padding: 5px;
    margin-right: 40px;
    margin-bottom: 15px;
    span {
      font-size: 20px;
      line-height: 120%;
      color: #ffffff;
    }
    &:hover {
      background: transparent;
      border: 1px solid #3C2C26;
      span {
        color: #000000;
      }
    }
  }
  &__info {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    &-icon {
      flex-shrink: 0;
      width: 27px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 14px;
      line-height: 120%;
      color: #333;
      margin-left: 15px;
      a {
        color: #776053;
        text-decoration: underline;
        text-underline-offset: 3px;
      }
    }
  }
  &__img {
    max-width: 1095px;
    position: relative;
    img {
      width: 100%;
    }

  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 96px / 4));
    gap: 32px;
    margin-top: 80px;
  }
  &-card {
    position: relative;
    text-align: center;
    filter: drop-shadow(30px 10px 40px rgba(60, 44, 38, 0.15));
    padding: 50px 30px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 50%;
      height: 100%;
      background: #ffffff;
      border-radius: 0 30px 30px 0;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 50%;
      height: 100%;
      background: #ffffff;
      border-radius: 30px 0 0 30px;
      pointer-events: none;
    }
    &__wrap {
      position: relative;
      z-index: 1;
    }
    &__img {
      max-width: 260px;
      height: 260px;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      margin: 0 auto;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
    &__text {
      font-size: 20px;
      line-height: 130%;
      color: #333333;
      margin-top: 20px;
    }
  }
}
.gallery {
  padding: 70px 0 280px;
  position: relative;
  &__line {
    position: absolute;
    top: -100px;
    left: 0;
    width: 100%;
  }
  &__inner {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    width: 480px;
    text-align: right;
    margin-right: 70px;
  }
  &__right {
    width: calc(100% - 550px);
  }
  &__title {
    font-family: 'Crimson';
    font-size: 40px;
    line-height: 120%;
    color: #333;
  }
  &__slider-nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 35px;
  }
  .swiper-button-prev {
    position: static;
    width: 42px;
    height: auto;
    margin-top: 0;
    &:after {
      display: none;
    }
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
    &:hover {
      svg path {
        fill: #776053;
      }
    }
  }
  .swiper-button-next {
    position: static;
    width: 42px;
    height: auto;
    margin-top: 0;
    &:after {
      display: none;
    }
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
    &:hover {
      svg path {
        fill: #776053;
      }
    }
  }
  .swiper-pagination {
    font-family: 'Crimson';
    position: static;
    width: auto;
    margin: 0 40px;
    &-current {
      font-size: 56px;
      line-height: 105%;
      color: #3C2C26;
    }
    &-total {
      font-size: 20px;
      line-height: 105%;
      color: #776053;
    }
  }
  .swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: center;
  }
  .swiper-slide {
    max-width: 435px;
    filter: drop-shadow(30px 20px 40px rgba(119, 96, 83, 0.25));
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-shadow-left,
    &-shadow-right {
      background: transparent;
    }
    &-active,
    &-next,
    &-next + .swiper-slide {
      opacity: 1;
      pointer-events: all;
    }
    &-active {
      z-index: 3;
    }
    &-next {
      z-index: 2;
      max-width: 316px;
      margin-left: -50px;
    }
    &-next + .swiper-slide {
      max-width: 262px;
      margin-left: -15px;
    }
  }
  &-card {
    img {
      width: 100%;
    }
  }
}
.modal {
  background: rgba(#000, .4);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 465px;
    max-width: 100%;
    position: relative;
    background: #F6EFDE;
    border-radius: 0px 0px 0px 100px;
    padding: 32px 45px 45px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close{
    position: absolute;
    top: -25px;
    right: -25px;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #776053;
    border-radius: 50%;
    padding: 3px;
    svg {
      width: 24px;
      height: auto;
    }
  }
  &__title {
    font-size: 24px;
    line-height: 120%;
    color: #333;
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 20px;
    &-group {
      border: none;
      padding: 0;
      margin: 0;
    }
    &-group input {
      width: 100%;
      height: 50px;
      font-size: 14px;
      line-height: 120%;
      color: #333;
      background: #FFF;
      border: 1px solid #776053;
      border-radius: 5px;
      padding: 5px 30px;
      &::placeholder {
        color: #858585;
      }
    }
    &-btn {
      width: 255px;
      height: 50px;
      background: #776053;
      border-radius: 50px;
      span {
        font-size: 20px;
        line-height: 120%;
        font-family: 'Crimson';
        color: #FFF;
      }
      &:hover {
        background: transparent;
        border: 1px solid #3C2C26;
        span {
          color: #000000;
        }
      }
    }
    &-info {
      position: relative;
      display: flex;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        opacity: 0;
        cursor: pointer;
      }
      input:checked ~ label:after {
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 7px;
      }
      label {
        position: relative;
        padding-left: 30px;
        font-size: 14px;
        line-height: 120%;
        color: #776053;
      }
      label:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 15px;
        height: 15px;
        border: 1px solid #776053;
        border-radius: 5px;
        transition: all 0.3s;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .main {
    &__cards {
      grid-template-columns: repeat(4, calc(25% - 60px / 4));
      gap: 20px;
    }
    &-card {
      padding: 30px;
      &__img {
        max-width: 200px;
        height: 200px;
      }
      &__text {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__right {
      .main__info {
        width: 300px;
        min-height: 80px;
      }
      .main__info-icon {
        width: 27px;
      }
      .main__info-text {
        font-size: 14px;
      }
    }
    &__title {
      font-size: 32px;
      span {
        font-size: 40px;
      }
    }
    &__text {
      font-size: 24px;
      margin-top: 30px;
    }
    &__group {
      margin-top: 60px;
    }
    &__btn {
      span {
        font-size: 18px;
      }
    }
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    }
  }
  .gallery {
    &__left {
      width: 420px;
      margin-right: 30px;
    }
    &__right {
      width: calc(100% - 450px);
    }
    &__title {
      font-size: 32px;
    }
    .swiper-slide {
      max-width: 300px;
      &-next {
        max-width: 250px;
      }
      &-next + .swiper-slide {
        max-width: 200px;
      }
    }
    .swiper-pagination {
      &-current {
        font-size: 48px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 120px 0 60px;
    &__wrap {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 32px 0;
    }
    &__right {
      width: 100%;
    }
    &__desc {
      max-width: 100%;
    }
    &__title {
      font-size: 24px;
      span {
        font-size: 32px;
      }
    }
    &__text {
      font-size: 18px;
    }
    &__cards {
      margin-top: 60px;
    }
    &-card {
      padding: 30px 20px;
      &__img {
        max-width: 160px;
        height: 160px;
      }
      &__text {
        margin-top: 15px;
      }
    }
  }
  .gallery {
    padding: 60px 0;
    &__line {
      display: none;
    }
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      text-align: start;
      margin: 0 0 50px;
    }
    &__right {
      width: 100%;
    }
    &__title {
      font-size: 24px;
    }
    .swiper-button-prev {
      width: 32px;
    }
    .swiper-button-next {
      width: 32px;
    }
    .swiper-slide {
      max-width: 100% !important;
      margin-left: 0 !important;
      &-next + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &__right {
      .main__info {
        position: static;
        width: 100%;
        min-height: 100%;
        background: transparent;
        padding: 0;
        margin-top: 10px;
      }
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
    }
    &-card {
      padding: 25px 16px;
      &__img {
        max-width: 120px;
        height: 120px;
      }
      &__text {
        font-size: 14px;
      }
    }
  }
  .gallery {
    .swiper-slide {
      max-width: 300px;
      opacity: 1 !important;
      pointer-events: all !important;
    }
  }
  .modal {
    &__dialog {
      border-radius: 0 0 0 60px;
      padding: 30px;
    }
    &__close {
      right: -5px;
    }
  }
}

@media screen and (max-width: 400px) {
  .main {
    &__cards {
      max-width: 200px;
      grid-template-columns: 100%;
      margin: 60px auto 0;
    }
  }
  .gallery {
    .swiper-slide {
      max-width: 100%;
    }
  }
}