.header {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 24px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
    margin-left: 45px;
  }
  &__logo {
    flex-shrink: 0;
    display: block;
    width: 207px;
    img {
      width: 100%;
    }
  }
  &__address {
    font-size: 14px;
    line-height: 120%;
    color: #333;
    margin-left: 45px;
  }
  &__mail {
    &-link {
      font-size: 14px;
      line-height: 120%;
      text-decoration: underline;
      text-underline-offset: 3px;
      color: #776053;
    }
    &-text {
      font-size: 14px;
      line-height: 120%;
      color: #333;
      margin-top: 3px;
    }
  }
  &__phones {
    display: flex;
    margin-left: 45px;
  }
  &__phone {
    color: #333;
    &:not(:first-child) {
      margin-left: 45px;
    }
    &-link {
      font-size: 14px;
      line-height: 120%;
      font-weight: 700;
    }
    &-link:hover {
      color: #776053;
    }
    &-text {
      font-size: 14px;
      line-height: 120%;
      margin-top: 3px;
    }
  }
  &__btn {
    font-family: 'Crimson';
    width: 194px;
    height: 49px;
    background: rgba(217, 217, 217, 0.00);
    border: 1px solid #3C2C26;
    border-radius: 50px;
    padding: 5px;
    margin-left: 45px;
    span {
      font-size: 20px;
      line-height: 120%;
      color: #000000;
    }
    &:hover {
      background: #3C2C26;
      span {
        color: #ffffff;
      }
    }
  }
  &__address-mob {
    display: none;
  }
}
.burger {
  display: none;
  position: relative;
  z-index: 11;
  width: 30px;
  height: 18px;
  cursor: pointer;
  margin-left: 25px;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #333;
    border-radius: 3px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span {
      background: rgba(255, 255, 255, 0.8);
    }
    span:nth-child(1) {
      top: 8px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 8px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__right {
      margin-left: 25px;
    }
    &__address {
      margin-left: 25px;
    }
    &__phones {
      margin-left: 25px;
    }
    &__phone {
      &:not(:first-child) {
        margin-left: 25px;
      }
    }
    &__btn {
      margin-left: 25px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .header {
    &__phones {
      display: block;
    }
    &__phone {
      &:not(:first-child) {
        margin: 15px 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__right {
      margin-left: 15px;
    }
    &__logo {
      width: 190px;
    }
    &__address {
      margin-left: 15px;
    }
    &__mail {
      &-text {
        font-size: 12px;
      }
    }
    &__phones {
      margin-left: 15px;
    }
    &__phone {
      &-text {
        font-size: 12px;
      }
    }
    &__btn {
      margin-left: 15px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__right {
      position: fixed;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100%;
      flex-direction: column;
      text-align: center;
      background: #776053;
      padding: 100px 15px 50px;
      margin: 0;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
    }
    &__mail,
    &__phone {
      &-link {
        color: rgba(255, 255, 255, 0.8);
        &:hover {
          color: #3C2C26;
        }
      }
      &-text {
        color: rgba(255, 255, 255, 0.8);
      }
    }
    &__phones {
      margin: 25px 0 0 0;
    }
    &__btn {
      border-color: rgba(255, 255, 255, 0.8);
      margin: 25px 0 0 0;
      span {
        color: rgba(255, 255, 255, 0.8);
      }
      &:hover {
        border-color: #3C2C26;
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__address {
      display: none;
    }
    &__address-mob {
      display: block;
      color: rgba(255, 255, 255, 0.8);
    }
    &__mail {
      margin-top: 25px;
    }
  }
}